import {useEffect, useMemo, useRef} from "react";
import cn from "classnames";
import {CSSTransition, SwitchTransition} from "react-transition-group";
import ArrowRightShort from "../../../assets/images/arrowRightShort.svg";
import {MAX_HINTS} from "../../../constants/game";
import {Image} from "../../Image";
import {Button} from "../../Button";
import {Text} from "../../Text";
import {Panel} from "../../Panel";
import {wait} from "../../../utils/wait";
import {useCallbackRef} from "../../../hooks/useCallbackRef";
import {ClipPath} from "../../ClipPath";
import {GameContent} from "../../GameContent";
import styles from './Step12345.module.scss'

const ANIMATION_DURATION = parseInt(styles.animationDuration)
const ANIMATION_NAME = styles.animationName

export function Step12345(props) {
    const {className, step, onPrevStep, onNextStep, onComplete, ...game} = props
    const {selectCell, selectChar, completeWord, reset} = game
    const isLoopingRef = useRef(false)
    const boardRef = useRef()
    const charsRef = useRef()
    const hintsRef = useRef()
    const actionsGroup1Ref = useRef()
    const actionsGroup2Ref = useRef()

    const selectCellRef = useCallbackRef(selectCell)
    const selectCharRef = useCallbackRef(selectChar)
    const completeWordRef = useCallbackRef(completeWord)
    const resetRef = useCallbackRef(reset)

    const [target, offset] = useMemo(() => {
        if (step === 1) {
            return [boardRef, [8.4, 15.4]]
        }
        if (step === 2) {
            return [charsRef, [8, 8]]
        }
        if (step === 3) {
            return [hintsRef, [7, 12]]
        }
        if (step === 4) {
            return [actionsGroup2Ref, [7, 8.5]]
        }
        if (step === 5) {
            return [actionsGroup1Ref, [7, 8.5]]
        }
        return [undefined, undefined]
    }, [step])

    function assertIsLooping() {
        if (!isLoopingRef.current) {
            throw new Error()
        }
    }

    async function processLoop() {
        try {
            await wait(400)
            assertIsLooping()
            selectCellRef([4, 4])
            await wait(800)
            assertIsLooping()
            selectCharRef('', -1)
            await wait(800)
            assertIsLooping()
            selectCellRef([6, 4])
            await wait(800)
            assertIsLooping()
            selectCharRef('', -1)
            await wait(800)
            assertIsLooping()
            completeWordRef()
            await wait(1200)
            assertIsLooping()
            resetRef()
            await wait(800)
            assertIsLooping()
            await processLoop()
        } catch {}
    }

    useEffect(() => {
        if (step === 1) {
            isLoopingRef.current = true
            processLoop()

            return () => {
                isLoopingRef.current = false
                reset()
            }
        }
    }, [step])

    return (
        <div className={cn(styles.wrapper, className)}>
            <ClipPath target={target} offset={offset} borderRadius={12}>
                {({ref, clipPath}) => <div ref={ref} style={{clipPath}} className={styles.backdrop}/>}
            </ClipPath>
            <GameContent
                boardRef={boardRef}
                charsRef={charsRef}
                hintsRef={hintsRef}
                actionsGroup1Ref={actionsGroup1Ref}
                actionsGroup2Ref={actionsGroup2Ref}
                {...game}
            />
            <SwitchTransition mode='out-in'>
                <CSSTransition key={step} timeout={ANIMATION_DURATION} classNames={ANIMATION_NAME}>
                    <div className={styles.hints}>
                        <Button className={styles.backButton} width={165} height={37} color="#0B4F38" onClick={onComplete}>
                            <Text as="span" weight={400} color="#FFFFFF">  </Text>
                        </Button>
                        {step === 1 && (
                            <>
                                <Panel className={styles.step1Panel1}>
                                    <Text>
                                        <Text as="span" weight={500}>&nbsp; </Text>,
                                        &nbsp;
                                        
                                        
                                        .
                                    </Text>
                                </Panel>
                                <Panel className={styles.step1Panel2}>
                                    <Text>
                                         <Text as="span" weight={500}>&nbsp;   
                                        &nbsp;</Text>. ,    <Text
                                        as="span"
                                        weight={500}>
                                        &nbsp; </Text>.
                                    </Text>
                                    <Button className={styles.step1Panel2NextButton} width={123} height={36}
                                            onClick={onNextStep}>
                                        <Image className={styles.nextButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                </Panel>
                            </>
                        )}
                        {step === 2 && (
                            <Panel className={styles.step2Panel}>
                                <Text>
                                    <Text as="span" weight={500}> </Text>  . {'\n'}&nbsp;&nbsp;  1&nbsp;&nbsp;2&nbsp; ! {'\n'}&nbsp;<Text as="span" weight={500}> </Text>, &nbsp;&nbsp; &nbsp;. {'\n'}     .
                                    {'\n\n'} &nbsp; <Text as="span" weight={500}></Text>!   &nbsp;  ,  &nbsp;<Text as="span" weight={500}> </Text>. &nbsp; , &nbsp;&nbsp;.
                                </Text>
                                <div className={styles.step2Buttons}>
                                    <Button className={styles.prevButton} width={80} height={36}
                                            onClick={onPrevStep}>
                                        <Image className={styles.prevButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                    <Button className={styles.nextButton} width={80} height={36}
                                            onClick={onNextStep}>
                                        <Image className={styles.nextButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                </div>
                            </Panel>
                        )}
                        {step === 3 && (
                            <Panel className={styles.step3Panel}>
                                <Text>
                                    &nbsp;&nbsp; &nbsp; &nbsp;<Text as="span"
                                                                                      weight={500}>{MAX_HINTS}&nbsp;</Text>,
                                     , <Text as="span" weight={500}>
                                    </Text> &nbsp;&nbsp; .
                                </Text>
                                <div className={styles.step3Buttons}>
                                    <Button className={styles.prevButton} width={80} height={36}
                                            onClick={onPrevStep}>
                                        <Image className={styles.prevButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                    <Button className={styles.nextButton} width={80} height={36}
                                            onClick={onNextStep}>
                                        <Image className={styles.nextButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                </div>
                            </Panel>
                        )}
                        {step === 4 && (
                            <Panel className={styles.step4Panel}>
                                <Text>
                                     &nbsp; <Text as="span" weight={500}></Text>!
                                    {'\n'}
                                    ,   <Text as="span" weight={500}>&nbsp;  &nbsp;</Text>.
                                    {'\n\n'}
                                       &nbsp;&nbsp;   &nbsp;<Text as="span"
                                                                                                    weight={500}></Text> .
                                </Text>
                                <div className={styles.step4Buttons}>
                                    <Button className={styles.prevButton} width={80} height={36}
                                            onClick={onPrevStep}>
                                        <Image className={styles.prevButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                    <Button className={styles.nextButton} width={80} height={36}
                                            onClick={onNextStep}>
                                        <Image className={styles.nextButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                </div>
                            </Panel>
                        )}
                        {step === 5 && (
                            <Panel className={styles.step5Panel}>
                                <Text>
                                    &nbsp; &nbsp;    &nbsp; <Text
                                    as="span"
                                    weight={500}>
                                    </Text> &nbsp; .  &nbsp; &nbsp; <Text
                                    as="span"
                                    weight={500}>5&nbsp;</Text>.
                                </Text>
                                <div className={styles.step5Buttons}>
                                    <Button className={styles.prevButton} width={80} height={36}
                                            onClick={onPrevStep}>
                                        <Image className={styles.prevButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                    <Button className={styles.nextButton} width={80} height={36}
                                            onClick={onNextStep}>
                                        <Image className={styles.nextButtonIcon} src={ArrowRightShort}/>
                                    </Button>
                                </div>
                            </Panel>
                        )}
                    </div>
                </CSSTransition>
            </SwitchTransition>
        </div>
    )
}